import React from 'react';
import { useState, useEffect } from 'react'
import OperationsList from '../../components/OperationsList';
import ModalOperationForm from '../../components/ModalOperationForm';
import operationService from '../../services/operations';
import { PlusCircleIcon } from '@heroicons/react/solid';
import { useHistory, useLocation } from 'react-router-dom';
import { SearchIcon } from '@heroicons/react/outline';

export default function Home() {
    const [total, setTotal] = useState(0)
    const newOperationInit = {concept: '', category: 'Crypto', amount: 0, type: 'Income', date: ''}
    const [operationList, setOperationList] = useState([])
    const [newOperationItem, setNewOperationItem] = useState(newOperationInit)
    const [isOpen, setIsOpen] = useState(false)
    const [isLoadingOperation, setIsLoadingOperation] = useState(false)
    const [isEdit, setIsEdit] = useState(false)
    const [cantOperations, setCantOperations] = useState(0)
    const history = useHistory()
    const location = useLocation()
    let query = new URLSearchParams(location.search)
    const page = parseInt(query.get("page")) || 0  
    const size = parseInt(query.get("size")) || 10 
    const [search, setSearch] = useState(query.get("search") || '')
    const [from, setFrom] = useState(query.get("from") || '')
    const [to, setTo] = useState(query.get("to") || '')
    query.set("page", page)
    query.set("size", size)
    const [querys, setQuerys] = useState(query)

    useEffect(()=> {
        setIsLoadingOperation(!isLoadingOperation)
        if (!querys){setQuerys(new URLSearchParams(location.search))}
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [location])

    useEffect(() => { 
        operationService.getOperationList(querys)
        .then(
            operations =>{
            const {rows, count, total} = operations || {}
            setCantOperations(count)
            setOperationList(rows?.map((operation)=>{ 
                if (!!operation["date"]){
                    operation["date"]=''}
                return operation}))
            setTotal(total)
            })
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [isLoadingOperation])

    useEffect(()=>{
        search ? querys.set("search", search) : querys.delete("search")
        from ? querys.set("from", from) : querys.delete("from")
        to ? querys.set("to", to) : querys.delete("to")
        setQuerys(querys)
        setIsLoadingOperation(!isLoadingOperation)
        history.push("/?"+querys) 
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [search, from, to])
    
    const handleSubmitOperationItem = (e) => {
        e.preventDefault()
        operationService.submitOperationItem(isEdit, newOperationItem)
        .then(res => {
            setIsEdit(false)
            setIsLoadingOperation(!isLoadingOperation)
            setIsOpen(!isOpen)
            setNewOperationItem(newOperationInit) })
    }

    const handleDeleteOperationItem = (id) => {
        operationService.deleteOperationItem(id)
        .then(res => {
            setIsLoadingOperation(!isLoadingOperation)})
      }
    
    const handleUpdateOperationItem = (operationItem) => {
        setIsEdit(!isEdit)
        setIsOpen(!isOpen)
        setNewOperationItem(operationItem)
    }

    const handleChangeInputOperationItem = (e) => {
        setNewOperationItem({ 
            ...newOperationItem, 
            [e.target.name]: e.target.value 
        })
    }

    const handleOpenModal = () => {
        setNewOperationItem(newOperationInit)
        setIsOpen(!isOpen)
    }

    const handleChangeSearch = (e) =>{
        setSearch(e.target.value)
    }

    const handleChangeSearchFrom = (e) =>{
        setFrom(e.target.value)
    }

    const handleChangeSearchTo = (e) =>{
        setTo(e.target.value)
    }

    const handleSearch = (e) =>{
        e.preventDefault() 
        search ? querys.set("search", search) : querys.delete("search")
        from ? querys.set("from", from) : querys.delete("from")
        to ? querys.set("to", to) : querys.delete("to")
        setQuerys(querys)
        setIsLoadingOperation(!isLoadingOperation)
        history.push("/?"+querys)  
    }
    return (
        <div className="container sm:mx-auto  text-gray-900">
            <div className='sm:mx-10 mx-1'>
                <div className=' flex flex-col mt-8'>
                    <h1 className='mx-auto text-lg sm:text-3xl'>
                        Your balance is 
                        <strong className={`${total >= 0 ? "text-green-500" : "text-red-500"}`}>
                        {" "}{total}
                        </strong>
                    </h1>
                    <div className='mx-auto'>
                        <PlusCircleIcon className=' cursor-pointer h-10 text-indigo-600' onClick={handleOpenModal} />
                    </div>
                </div>
                <ModalOperationForm 
                onSubmit={handleSubmitOperationItem} 
                onChange={handleChangeInputOperationItem} 
                newOperationItem={newOperationItem} 
                isEdit={isEdit} 
                isOpen={isOpen} 
                setIsOpen={setIsOpen} 
                setIsEdit={setIsEdit}
                />
                <form className='flex flex-row justify-center' onSubmit={handleSearch}>
                    <input type="text" placeholder='Search by concept or category' className=' w-24 sm:text-lg sm:w-96 p-1 my-5 bg-white h-11 border-indigo-600 border-t border-l border-b rounded-l-lg outline-none text-xs' 
                    value={search} onChange={handleChangeSearch} />
                    <button className=''>
                        <SearchIcon className='h-11 sm:text-lg py-3 pr-2 bg-white text-indigo-600 border-indigo-600 border-t border-b sm:hover:text-indigo-800 text-xs' />
                    </button>
                    <input type={from ? "date" : "text"}  placeholder='From: dd/mm/aaaa' 
                    onFocus={
                    (e)=> {
                    e.currentTarget.type = "date";
                    e.currentTarget.focus();
                    }} 
                    className=' w-28 sm:w-48 sm:text-lg p-1 my-5 -ml-1 bg-white h-11 border-indigo-600 border outline-none text-xs' 
                    value={from} onChange={handleChangeSearchFrom} />
                    <input type={to ? "date" : "text"} placeholder='To: dd/mm/aaaa' 
                    onFocus={
                    (e)=> {
                    e.currentTarget.type = "date";
                    e.currentTarget.focus();
                    }} 
                    className=' w-28 sm:w-48  sm:text-lg p-1 my-5 -ml-1 bg-white h-11 border-indigo-600 border rounded-r-lg outline-none text-xs' 
                    value={to} onChange={handleChangeSearchTo} />
                </form>
                <OperationsList 
                operationList={operationList} 
                onEdit={handleUpdateOperationItem} 
                onDelete={handleDeleteOperationItem}
                page={page}
                size={size}
                cantOperations={cantOperations}
                isSort={isLoadingOperation}
                setIsSort={setIsLoadingOperation}
                querys={querys}
                setQuerys={setQuerys}
                />   
            </div>
        </div>
    )
    
}
